import { useState, useEffect } from 'react';
import { useAuthStore } from '../../stores/authStore';
import { useBackendStore, validateBackendUrl } from '../../stores/backendStore';
import { Modal, Button, Input } from '../ui';
import { UserPlus, LogIn, Eye, EyeOff, Check, X } from 'lucide-react';
import { cn } from '../../utils/helpers';

interface AuthModalProps {
  isOpen: boolean;
  onClose: () => void;
  defaultTab?: 'login' | 'register';
}

export default function AuthModal({ isOpen, onClose, defaultTab = 'login' }: AuthModalProps) {
  const [activeTab, setActiveTab] = useState<'login' | 'register'>(defaultTab);
  const { login, register, checkUsername } = useAuthStore();
  const { backendUrl, setBackendUrl } = useBackendStore();

  // Backend URL state
  const [backendUrlInput, setBackendUrlInput] = useState(backendUrl);
  const [backendUrlError, setBackendUrlError] = useState('');

  // Login form state
  const [loginUsername, setLoginUsername] = useState('');
  const [loginPassword, setLoginPassword] = useState('');
  const [loginLoading, setLoginLoading] = useState(false);
  const [loginError, setLoginError] = useState('');

  // Sync backendUrl from store when modal opens
  useEffect(() => {
    if (isOpen) {
      setBackendUrlInput(backendUrl);
      setBackendUrlError('');
    }
  }, [isOpen, backendUrl]);

  // Register form state
  const [regUsername, setRegUsername] = useState('');
  const [regPassword, setRegPassword] = useState('');
  const [regConfirmPassword, setRegConfirmPassword] = useState('');
  const [regName, setRegName] = useState('');
  const [regEmail, setRegEmail] = useState('');
  const [regInviteCode, setRegInviteCode] = useState(''); // 
  const [regLoading, setRegLoading] = useState(false);
  const [regError, setRegError] = useState('');
  const [usernameAvailable, setUsernameAvailable] = useState<boolean | null>(null);
  const [showPassword, setShowPassword] = useState(false);

  const handleLogin = async () => {
    // Validate backend URL first
    const validation = setBackendUrl(backendUrlInput);
    if (!validation.valid) {
      setBackendUrlError(validation.error || '');
      return;
    }
    setBackendUrlError('');

    if (!loginUsername || !loginPassword) {
      setLoginError('');
      return;
    }

    setLoginLoading(true);
    setLoginError('');

    const success = await login(loginUsername, loginPassword);

    setLoginLoading(false);

    if (success) {
      onClose();
      setLoginUsername('');
      setLoginPassword('');
      // 
      window.location.hash = '#chat';
    } else {
      setLoginError('');
    }
  };

  const handleRegister = async () => {
    // Validate backend URL first
    const validation = setBackendUrl(backendUrlInput);
    if (!validation.valid) {
      setBackendUrlError(validation.error || '');
      return;
    }
    setBackendUrlError('');

    // Validation
    if (!regUsername || !regPassword || !regName) {
      setRegError('');
      return;
    }

    if (regUsername.length < 3) {
      setRegError('3');
      return;
    }

    if (regPassword.length < 6) {
      setRegError('6');
      return;
    }

    if (regPassword !== regConfirmPassword) {
      setRegError('');
      return;
    }

    if (usernameAvailable === false) {
      setRegError('');
      return;
    }

    setRegLoading(true);
    setRegError('');

    const success = await register(regUsername, regPassword, regName, regEmail || undefined, regInviteCode || undefined);
    
    setRegLoading(false);

    if (success) {
      onClose();
      // Reset form
      setRegUsername('');
      setRegPassword('');
      setRegConfirmPassword('');
      setRegName('');
      setRegEmail('');
      setUsernameAvailable(null);
      // 
      window.location.hash = '#chat';
    } else {
      setRegError('');
    }
  };

  const checkUsernameAvailability = async (username: string) => {
    if (username.length < 3) {
      setUsernameAvailable(null);
      return;
    }
    const available = await checkUsername(username);
    setUsernameAvailable(available);
  };

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title={activeTab === 'login' ? '' : ''}
      size="md"
    >
      <div className="space-y-4">
        {/* Tab Switcher */}
        <div className="flex rounded-lg bg-[var(--tg-secondary-bg-color)] p-1">
          <button
            onClick={() => {
              setActiveTab('login');
              setLoginError('');
              setRegError('');
            }}
            className={cn(
              'flex-1 flex items-center justify-center gap-2 py-2 rounded-md text-sm font-medium transition-colors',
              activeTab === 'login'
                ? 'bg-[var(--tg-button-color)] text-[var(--tg-button-text-color)]'
                : 'text-[var(--tg-text-color)] hover:bg-black/5 dark:hover:bg-white/5'
            )}
          >
            <LogIn className="w-4 h-4" />
            
          </button>
          <button
            onClick={() => {
              setActiveTab('register');
              setLoginError('');
              setRegError('');
            }}
            className={cn(
              'flex-1 flex items-center justify-center gap-2 py-2 rounded-md text-sm font-medium transition-colors',
              activeTab === 'register'
                ? 'bg-[var(--tg-button-color)] text-[var(--tg-button-text-color)]'
                : 'text-[var(--tg-text-color)] hover:bg-black/5 dark:hover:bg-white/5'
            )}
          >
            <UserPlus className="w-4 h-4" />
            
          </button>
        </div>

        {activeTab === 'login' ? (
          <div className="space-y-4">
            {/* Backend URL Input */}
            <Input
              label=""
              placeholder="http://localhost:8080"
              value={backendUrlInput}
              onChange={(e) => {
                setBackendUrlInput(e.target.value);
                setBackendUrlError('');
              }}
              onBlur={() => {
                const result = validateBackendUrl(backendUrlInput);
                if (!result.valid) {
                  setBackendUrlError(result.error || '');
                } else {
                  setBackendUrlError('');
                }
              }}
            />
            {backendUrlError && (
              <p className="text-xs text-red-500 -mt-2">{backendUrlError}</p>
            )}
            <p className="text-xs text-[var(--tg-hint-color)] -mt-2">
               localhost, 127.0.0.1, IP 
            </p>

            <Input
              label=""
              placeholder=""
              value={loginUsername}
              onChange={(e) => setLoginUsername(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && handleLogin()}
            />
            <div className="relative">
              <Input
                label=""
                type={showPassword ? 'text' : 'password'}
                placeholder=""
                value={loginPassword}
                onChange={(e) => setLoginPassword(e.target.value)}
                onKeyDown={(e) => e.key === 'Enter' && handleLogin()}
              />
              <button
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-3 top-[34px] text-[var(--tg-hint-color)] hover:text-[var(--tg-text-color)]"
              >
                {showPassword ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
              </button>
            </div>

            {loginError && (
              <div className="p-3 bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400 text-sm rounded-lg">
                {loginError}
              </div>
            )}

            <Button
              onClick={handleLogin}
              isLoading={loginLoading}
              className="w-full"
            >
              
            </Button>

            <p className="text-xs text-[var(--tg-hint-color)] text-center">
              
            </p>
          </div>
        ) : (
          <div className="space-y-4">
            {/* Backend URL Input */}
            <Input
              label=""
              placeholder="http://localhost:8080"
              value={backendUrlInput}
              onChange={(e) => {
                setBackendUrlInput(e.target.value);
                setBackendUrlError('');
              }}
              onBlur={() => {
                const result = validateBackendUrl(backendUrlInput);
                if (!result.valid) {
                  setBackendUrlError(result.error || '');
                } else {
                  setBackendUrlError('');
                }
              }}
            />
            {backendUrlError && (
              <p className="text-xs text-red-500 -mt-2">{backendUrlError}</p>
            )}
            <p className="text-xs text-[var(--tg-hint-color)] -mt-2">
               localhost, 127.0.0.1, IP 
            </p>

            <div className="relative">
              <Input
                label=" *"
                placeholder="3"
                value={regUsername}
                onChange={(e) => {
                  setRegUsername(e.target.value);
                  checkUsernameAvailability(e.target.value);
                }}
              />
              {regUsername.length >= 3 && (
                <span className="absolute right-3 top-[34px]">
                  {usernameAvailable === true ? (
                    <Check className="w-4 h-4 text-green-500" />
                  ) : usernameAvailable === false ? (
                    <X className="w-4 h-4 text-red-500" />
                  ) : null}
                </span>
              )}
            </div>
            {usernameAvailable === false && (
              <p className="text-xs text-red-500 -mt-2"></p>
            )}

            <div className="relative">
              <Input
                label=" *"
                type={showPassword ? 'text' : 'password'}
                placeholder="6"
                value={regPassword}
                onChange={(e) => setRegPassword(e.target.value)}
              />
              <button
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-3 top-[34px] text-[var(--tg-hint-color)] hover:text-[var(--tg-text-color)]"
              >
                {showPassword ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
              </button>
            </div>

            <Input
              label=" *"
              type="password"
              placeholder=""
              value={regConfirmPassword}
              onChange={(e) => setRegConfirmPassword(e.target.value)}
            />
            {regPassword && regConfirmPassword && regPassword !== regConfirmPassword && (
              <p className="text-xs text-red-500 -mt-2"></p>
            )}

            <Input
              label=" *"
              placeholder=""
              value={regName}
              onChange={(e) => setRegName(e.target.value)}
            />

            <Input
              label=""
              type="email"
              placeholder=""
              value={regEmail}
              onChange={(e) => setRegEmail(e.target.value)}
            />

            {/*  -  */}
            <div className="space-y-2">
              <label className="text-sm font-medium"> *</label>
              <p className="text-xs text-[var(--tg-hint-color)]">
                
              </p>
              <Input
                placeholder=""
                value={regInviteCode}
                onChange={(e) => setRegInviteCode(e.target.value)}
              />
            </div>

            {regError && (
              <div className="p-3 bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400 text-sm rounded-lg">
                {regError}
              </div>
            )}

            <Button
              onClick={handleRegister}
              isLoading={regLoading}
              className="w-full"
            >
              
            </Button>

            <p className="text-xs text-[var(--tg-hint-color)] text-center">
              
            </p>
          </div>
        )}
      </div>
    </Modal>
  );
}
